<template>
  <div class="jd-options">
    <div @click="onItemClick" v-for="(item) in dataSource" :key="item.id" class="jd-option-item">
      <img :src="item.icon" alt />
      <p>{{item.title}}</p>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dataSource: []
    }
  },
  created() {
    this.getOptionsData()
  },
  methods: {
    getOptionsData() {
      this.$http.get('/data.json').then(data => {
        this.dataSource = data.data.dataSource
      }).catch(err => {
        console.log(err)
      })
    },
    onItemClick() {
      this.$router.push({
        name: 'goodsList',
        params: {
          routerType: 'push'
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.jd-options {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  padding-bottom: 14/20rem;
  > .jd-option-item {
    width: 20%;
    text-align: center;
    margin-top: 0.6rem;
    img {
      width: 60%;
    }
    p {
      font-size: 1rem;
      margin-top: 0.2rem;
    }
  }
}
</style>
