<template>
  <div class="count-down">
    <span class="count-down-end-time">{{endHours}}点场</span>
    <span class="count-down-surplus-time">{{surplus|filterTime}}</span>
  </div>
</template>
<script>
export default {
  props: {
    endHours: {
      type: Number,
      required: true,
      default: 0
    }
  },
  data() {
    return {
      surplus: '',
      diffSeconds: 0,
      interval: null
    }
  },
  methods: {
    computedSurplusTime() {
      const date = new Date()
      if (date.getHours() > this.endHours) {
        this.surplus = '活动结束'
        clearInterval(this.interval)
        return ''
      }
      if (date.getHours() === this.endHours) {
        this.surplus = '活动进行中'
        return
      }
      const diffHours = this.endHours - date.getHours() - 1
      const diffMinutes = 60 - date.getMinutes() - 1
      const diffSeconds = 60 - date.getSeconds()
      this.diffSeconds = diffHours * 3600 + diffMinutes * 60 + diffSeconds
      this.interval = setInterval(() => {
        this.diffSeconds -= 1
      }, 1000)
    }
  },
  created() {
    this.computedSurplusTime()
  },
  watch: {
    diffSeconds: function (newV) {
      const hours = Math.floor(newV / 3600)
      const minutes = Math.floor(newV / 60) % 60
      const seconds = Math.floor(newV % 60)
      this.surplus = hours + ':' + minutes + ':' + seconds
    }
  }
}
</script>
<style lang="less" scoped>
.count-down {
  display: inline-block;
  font-size: 0.8rem;
  margin-left: 1.6rem;
  border: 1px solid red;
  border-radius: 0.2rem;
  span {
    display: inline-block;
    padding: 0.2rem;
  }
  .count-down-end-time {
    background-color: red;
    border-top-left-radius: 0.2rem;
    border-bottom-left-radius: 0.2rem;
    color: #fff;
  }
  .count-down-surplus-time {
    background-color: #fff;
    border-top-right-radius: 0.2rem;
    border-bottom-right-radius: 0.2rem;
    color: #555;
  }
}
</style>
