<template>
  <div class="home" @click="onScroll" ref="home">
    <div class="home-content">
      <!-- 头部 -->
      <navgation-bar :isShowBack="false">
        <!-- 左侧插槽 -->
        <template v-slot:nav-left>
          <img style="width:1.8rem" src="../assets/img/more-white.svg" />
        </template>
        <!-- 中间插槽 -->
        <template v-slot:nav-center>
          <search :navBarStyle="navBarStyle"></search>
        </template>
        <!-- 右侧插槽 -->
        <template v-slot:nav-right>
          <img src="../assets/img/message-white.svg" />
        </template>
      </navgation-bar>
      <my-swiper :height="swiperHeight" :swiperImgs="swiperImgs"></my-swiper>
      <!-- 520活动 -->
      <activity>
        <div class="activity-520">
          <img :src="item" v-for="(item,index) in activityDatas" :key="index" alt />
        </div>
      </activity>
      <!-- 京东选项 -->
      <home-options></home-options>
      <!-- 秒杀 -->
      <seconds :secondsList="secondsList"></seconds>
      <!-- 拼购节 -->
      <activity>
        <div class="activity-pin-gou-jie">
          <img src="../assets/haoHuoQiang.gif" />
        </div>
      </activity>
      <!-- 使用瀑布流的方式 -->
      <goods :layoutType="'2'"></goods>
    </div>
  </div>
</template>
<script>
import MySwiper from './swiper/MySwiper.vue'
import Activity from './currency/Activity.vue'
import HomeOptions from './currency/HomeOptions.vue'
import Seconds from './currency/Seconds.vue'
import Goods from './goods/Goods.vue'
import NavgationBar from './currency/NavgationBar.vue'
import Search from './currency/Search.vue'
export default {
  components: {
    MySwiper,
    Activity,
    HomeOptions,
    Seconds,
    Goods,
    NavgationBar,
    Search
  },
  created() {
    this.initData()
  },
  activated() {
    this.$refs.home.scrollTop = document.documentElement.scrollTop
  },
  methods: {
    onScroll() {

    },
    initData() {
      this.$http.get('/data.json').then(data => {
        this.swiperImgs = data.data.swiperImgs
        this.activityDatas = data.data.activityData
        this.secondsList = data.data.seconds
      }).catch(err => {
        console.log(err)
      })
    }
  },
  data() {
    return {
      swiperImgs: [],
      activityDatas: [],
      secondsList: [],
      goodsList: [],
      swiperHeight: '184px',
      navBarStyle: {},
      opacity: 0
    }
  }
}
</script>
<style lang="less">
.home {
  position: absolute;
  width: 100%;
  height: 100%;
}
.activity-520 {
  border-radius: 0.8rem;
  overflow: hidden;
  img {
    width: 33.33%;
    height: 140/20rem;
  }
}
.activity-pin-gou-jie {
  border-radius: 0.8rem;
  overflow: hidden;
  img {
    width: 100%;
    height: 140/20rem;
  }
}
</style>
